import React, {useContext, useEffect, useState} from 'react';
import './css/style.css';
import 'jquery/dist/jquery.min.js'
import 'bootstrap/dist/js/bootstrap.min.js'
import './assets/css/nucleo-icons.css';
import "./assets/js/soft-ui-dashboard.js"
import './assets/css/nucleo-svg.css';
import './assets/css/soft-ui-dashboard.css';
import { Route, useNavigate, Routes} from "react-router-dom";
import Groups from "./pages/Groups";
import Schedules from "./pages/Schedules";
import Teacher from "./pages/Teacher";
import Home from "./pages/Home";
import SchedulesTeacher from "./pages/SchedulesTeacher";
import {ThemeContext, themes} from "./contexts/ThemeContext";


function App() {
    const context = useContext(ThemeContext);
    const navigate = useNavigate();

    useEffect(() => {
        context.setTypeShedId('home');
        if(localStorage.getItem('theme') != null){
            context.setTheme(localStorage.getItem('theme'));

        }else{
            localStorage.setItem('theme', themes.light);
            context.setTheme(localStorage.getItem('theme'));
        }
        if(localStorage.getItem('darkMode') != null){
            context.setDarkMode(JSON.parse(localStorage.getItem('darkMode')));
        }else{
            localStorage.setItem('darkMode', false);
            context.setDarkMode(JSON.parse(localStorage.getItem('darkMode')));
        }
        if(localStorage.getItem('themeColor') == null){
            localStorage.setItem('themeColor', 'bg-gradient-info');
            context.setThemeColor('bg-gradient-info');
        }
        if(localStorage.getItem('themeImg') == null){
            localStorage.setItem('themeImg', 'https://alfin-dk.ru/soft/assets/img/curved-images/curved14.jpg');
            context.setThemeImg(localStorage.getItem('themeImg'));
        }
        navigating()
    }, [])

    const navigating = () => {
        if(context.typeShed === 'home' && context.themeImg !== null){
            navigate("/");
        }
        if(context.typeShed === 'student' && context.group == null){
            navigate("/groups");
        }
        if(context.typeShed === 'student' && context.group != null){
            navigate(`/group/${context.group}`);
        }
        if(context.typeShed === 'teacher' && context.teacher == null){
            navigate("/teacher");
        }
        if(context.typeShed === 'teacher' && context.teacher != null){
            navigate(`/teacher/${context.teacher}`);
        }
    }

    return (
    <div className="App">
        <Routes>
                <Route path="/groups" element={<Groups/>} />
                <Route path="/group/:group" element={<Schedules/>} />
                <Route path="/teacher" element={<Teacher/>} />
                <Route path="/teacher/:teacher" element={<SchedulesTeacher/>} />
                <Route path="/" element={<Home/>}/>
        </Routes>
    </div>
  );
}

export default App;
