import React, {useEffect, useState} from 'react';
// eslint-disable-next-line no-unused-vars
import {Link, useNavigate, useParams} from "react-router-dom";
import Nav from "../components/Nav";
import Card from "../components/Card";
import Body from "../components/Body";
import ScheduleListTeacher from "../components/teacher/ScheduleListTeacher";
import Loader from "../components/Loader";
import {CSSTransition} from "react-transition-group";
import { ThemeContext, themes } from '../contexts/ThemeContext';
import NightsStayIcon from '@mui/icons-material/NightsStay';
import LightModeIcon from '@mui/icons-material/LightMode';
import ReloadIcon from "../components/ReloadIcon";
import Menu from "../components/Menu";
import Error from "../components/Error";

const SchedulesTeacher = (UUID) => {
    const [error, setError] = useState(false);
    let navigate = useNavigate();
    const teacher = useParams();
    const [isLoading, setLoading] = useState(true);
    const [schedule, setSchedule] = useState({});

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const schedulesFetch = async () => {
        await fetch(`https://api.alfin-dk.ru/urtk/teacher/shedule/get?teacher_uuid=${teacher.teacher == null ? UUID.UUID : teacher.teacher}`).then(res => res.json()).then(
            res => {
                if(res.error === "Bad group name see ip:port/urtk/groups/get"){
                    navigate("/");
                }else{
                    setSchedule(res);
                    setLoading(false);
                }
            }
        ).catch(() => {
            setError(true);
        })
    }

    useEffect(() => {
        schedulesFetch();
    }, [])

    return (
        <div>
            <CSSTransition
                in={!isLoading}
                timeout={500}
                classNames="body"
                unmountOnExit
            >
                <ThemeContext.Consumer>
                    {({ changeTheme, theme, darkMode, dayWeek,setDayWeekID, themeColor, setThemeColor, setThemeImg }) => (
                        <Body>
                            <Nav>
                            </Nav>
                            <Card themeColor={themeColor} title={dayWeek} subtitle={<Link onClick={()=>{ localStorage.removeItem('teacher'); }} style={{color: '#fff'}} to='/teacher'>{schedule.teacher}<ReloadIcon/></Link>} >
                                <ScheduleListTeacher themeColor={themeColor} setDayWeekID={setDayWeekID} darkMode={darkMode} theme={theme} schedules={schedule.schedule}/>
                            </Card>
                        </Body>
                    )}
                </ThemeContext.Consumer>
            </CSSTransition>

            <CSSTransition
                in={isLoading}
                timeout={500}
                classNames="loader"
                unmountOnExit
            >
                <ThemeContext.Consumer>
                    {({darkMode, themeColor }) => {
                        if(error){
                            return(<Error themeColor={themeColor} mode={darkMode} localStore={'teacher'} url={'teacher'}/>);
                        }else {
                            return(<Loader mode={darkMode} themeColor={themeColor}/>);
                        }
                    }}
                </ThemeContext.Consumer>
            </CSSTransition>
        </div>


    );
};

export default SchedulesTeacher;