import React, { useState, useEffect } from 'react';
import { ThemeContext, themes } from './ThemeContext';
import {useNavigate} from "react-router-dom";


export default function ThemeContextWrapper(props) {
    const [theme, setTheme] = useState();
    const [darkMode, setDarkMode] = useState();
    const [idDayWeek, setIdDayWeek] = useState(0);
    const [dayWeek, setDayWeek] = useState('');
    const [typeShed, setTypeShed] = useState(localStorage.getItem('typeShed'));
    const [group, setGroup] = useState(localStorage.getItem('group'));
    const [teacher, setTeacher] = useState(localStorage.getItem('teacher'));
    const [themeColor, setThemeColor] = useState(localStorage.getItem('themeColor'));
    const [themeColorHEX, setThemeColorHEX] = useState(localStorage.getItem('themeColor'));
    const [themeImg, setThemeImg] = useState(localStorage.getItem('themeImg'));

    function setDayWeekID(id) {
        setIdDayWeek(id);
    }

    function setGroupId(id) {
        localStorage.setItem('group', id);
        setGroup(id);
    }
    function setTeacherId(id) {
        localStorage.setItem('teacher', id);
        setTeacher(id);
    }
    function setTypeShedId(type) {
        localStorage.setItem('typeShed', type);
        setTypeShed(type);
    }

    function changeTheme(theme) {
        setTheme(theme);
        localStorage.setItem('theme', theme);
        setDarkMode(!darkMode);
        localStorage.setItem('darkMode', !darkMode);
    }

    useEffect(() => {
        // eslint-disable-next-line default-case
        switch (themeColor) {
            case 'bg-gradient-info':
                setThemeColorHEX({color: '#239afe'});
                break;
            case 'bg-gradient-primary':
                setThemeColorHEX({color: '#bf15a3'});
                break;
            case 'bg-gradient-warning':
                setThemeColorHEX({color: '#faa437'});
                break;
            case 'bg-gradient-success':
                setThemeColorHEX({color: '#54cb32'});
                break;
            case 'bg-gradient-danger':
                setThemeColorHEX({color: '#f42f39'});
                break;
        }
    }, [themeColor]);

    useEffect(() => {
        // eslint-disable-next-line default-case
        switch (idDayWeek) {
            case 0:
                setDayWeek('Понедельник');
                break;
            case 1:
                setDayWeek('Вторник');
                break;
            case 2:
                setDayWeek('Среда');
                break;
            case 3:
                setDayWeek('Четверг');
                break;
            case 4:
                setDayWeek('Пятница');
                break;
            case 5:
                setDayWeek('Суббота');
                break;
        }
    }, [idDayWeek]);

    useEffect(() => {
        // eslint-disable-next-line default-case
        switch (theme) {
            case themes.light:
                document.body.classList.remove('dark-body');
                document.body.classList.add('ligth-body');
                break;
            case themes.dark:
                document.body.classList.remove('ligth-body');
                document.body.classList.add('dark-body');
                break;
        }
    }, [theme]);


    return (
        <ThemeContext.Provider value={{setDarkMode, setTheme,themeColorHEX, themeColor, setThemeColor,themeImg,setThemeImg, typeShed, group, teacher, theme, darkMode, dayWeek, changeTheme, setDayWeekID, setGroupId,setTeacherId,setTypeShedId}}>
            {props.children}
        </ThemeContext.Provider>
    );
}