import React, {useContext, useEffect, useState} from 'react';
import Body from "../components/Body";
import Nav from "../components/Nav";
import {ThemeContext, themes} from "../contexts/ThemeContext";
import LightModeIcon from "@mui/icons-material/LightMode";
import NightsStayIcon from "@mui/icons-material/NightsStay";
import Card from "../components/Card";
import {Link} from "react-router-dom";
import {CSSTransition} from "react-transition-group";

import Menu from "../components/Menu";
import Error from "../components/Error";
import Loader from "../components/Loader";

const Home = () => {
    const [isLoading, setLoad] = useState(false);
    const error = false;

    useEffect(()=>{
        setTimeout(()=>{
            setLoad(true);
        },5)
    },[])

    return (
        <>
        <CSSTransition
            in={isLoading}
            timeout={5000}
            classNames="body"
            unmountOnExit
        >
        <ThemeContext.Consumer>
            {({ theme, darkMode, setTypeShedId, themeColor}) => (
                <Body>
                    <Nav>
                    </Nav>
                    <Card themeColor={themeColor} title="Расписание УРТК" subtitle="Добро пожаловать!" >
                        <div className="container">
                            <div className="row mt-lg-n10 mt-md-n11 mt-n10">
                                <div className="col-xl-4 col-lg-5 col-md-7 mx-auto">
                                    <div className={`card z-index-0 ${theme}`}>
                                        <div className={`card-header text-center pt-4`}>
                                            <h5 style={darkMode ? {color: '#dee1e4'} : {color: '#354766'}} className="card-text">Кто вы?</h5>
                                        </div>
                                        <div className="container-fluid" style={{marginBottom: '2rem'}}>
                                            <div className="nav-wrapper position-relative end-0">
                                                <Link onClick={()=>{setTypeShedId('student')
                                                        }} to="/groups"
                                                      className={`btn ${themeColor} w-100 mt-4 mb-0`}>
                                                    Студент
                                                </Link>
                                                <Link onClick={()=>{setTypeShedId('teacher')
                                                        }} to="/teacher" className={`btn ${themeColor} w-100 mt-4 mb-0`}>
                                                    Преподаватель
                                                </Link>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Card>
                </Body>
            )}
        </ThemeContext.Consumer>
        </CSSTransition>
            <CSSTransition
                in={!isLoading}
                timeout={500}
                classNames="loader"
                unmountOnExit
            >
                <ThemeContext.Consumer>
                    {({darkMode, themeColor }) => {
                        if(error){
                            return(<Error themeColor={themeColor} mode={darkMode} localStore={'typeShed'} url={''}/>);
                        }else {
                            return(<Loader mode={darkMode} themeColor={themeColor}/>);
                        }
                    }}

                </ThemeContext.Consumer>
            </CSSTransition>
        </>
    );
};

export default Home;