import React, {useEffect, useState} from 'react';
import {CSSTransition} from "react-transition-group";
import {ThemeContext, themes} from "../contexts/ThemeContext";
import Body from "../components/Body";
import Nav from "../components/Nav";
import LightModeIcon from "@mui/icons-material/LightMode";
import NightsStayIcon from "@mui/icons-material/NightsStay";
import Card from "../components/Card";
import ListGroups from "../components/student/ListGroups";
import Loader from "../components/Loader";
import ListTeacher from "../components/teacher/ListTeacher";
import {Link} from "react-router-dom";
import ReloadIcon from "../components/ReloadIcon";
import Menu from "../components/Menu";
import Error from "../components/Error";

const Teacher = () => {
    const [error, setError] = useState(false);
    const [teachers, setTeachers] = useState({});
    const [isLoading, setLoading] = useState(true);


    const getTeacher = async () => {
        await fetch("https://api.alfin-dk.ru/urtk/teachers/get").then(res => res.json()).then(
            res => {
                setTeachers(res);
                setLoading(false);
            }
        ).catch(() => {
            setError(true);
        })
    }


    useEffect(() => {
        getTeacher();
    }, [])
    return (
        <>
            <CSSTransition
                in={!isLoading}
                timeout={500}
                classNames="body"
                unmountOnExit
            >
                <ThemeContext.Consumer>
                    {({ changeTheme, theme, darkMode, setTeacherId,themeColor, setThemeColor, setThemeImg }) => (
                        <Body>
                            <Nav>
                            </Nav>
                            <Card themeColor={themeColor} title="Расписание УРТК" subtitle={<Link onClick={()=>{ localStorage.removeItem('typeShed'); }} to={'/'} style={{color:"#fff"}}>Главная<ReloadIcon/></Link>} >
                                <ListTeacher themeColor={themeColor} setTeacherId={setTeacherId} title="Выберете преподавателя" darkMode={darkMode} theme={theme} json={teachers}/>
                            </Card>
                        </Body>
                    )}
                </ThemeContext.Consumer>
            </CSSTransition>

            <CSSTransition
                in={isLoading}
                timeout={500}
                classNames="loader"
                unmountOnExit
            >
                <ThemeContext.Consumer>
                    {({darkMode, themeColor }) => {
                        if(error){
                            return(<Error themeColor={themeColor} mode={darkMode} localStore={'typeShed'} url={''}/>);
                        }else {
                            return(<Loader mode={darkMode} themeColor={themeColor}/>);
                        }
                    }}

                </ThemeContext.Consumer>
            </CSSTransition>
        </>

    );
};

export default Teacher;