import React from 'react';
import {ErrorOutline} from "@mui/icons-material";
import {useNavigate} from "react-router-dom";

const Error = ({mode, localStore, url, themeColor}) => {
    let navigate = useNavigate();
    const back = ()=>{
        localStorage.removeItem(localStore);
        navigate(`/${url}`);
    }

    return (
        <div className={mode ? "contLoaderDark" : "contLoader"}>
            <div className="error" style={{textAlign:'center'}}>
                <div style={{textAlign:'center'}}>
                    <ErrorOutline style={{width:'5rem', height:'5rem', color:'red'}}/>
                </div>
                <span>Ошибка загрузки данных</span><br/>
                <span style={{marginTop:'.5rem'}} className={`btn ${themeColor}`} onClick={back}>Вернуться назад</span>
            </div>
        </div>
    );
};

export default Error;