import React, {useEffect, useState} from 'react';
import ListGroups from "../components/student/ListGroups";
import Nav from "../components/Nav";
import Card from "../components/Card";
import Body from "../components/Body";
import Loader from "../components/Loader";
import { CSSTransition } from 'react-transition-group';
import { ThemeContext, themes } from '../contexts/ThemeContext';
import {Link} from "react-router-dom";
import ReloadIcon from "../components/ReloadIcon";
import Error from "../components/Error";

const Groups = () => {
    const [error, setError] = useState(false);
    const [groups, setGroups] = useState({});
    const [isLoading, setLoading] = useState(true);


    const getGroups = async () => {
        await fetch("https://api.alfin-dk.ru/urtk/groups/get").then(res => res.json()).then(
            res => {
                setGroups(res);
                setLoading(false);
            }
        ).catch(() => {
            setError(true);
        })
    }


    useEffect(() => {
        getGroups();
    }, [])
    return (
            <>
                <CSSTransition
                        in={!isLoading}
                        timeout={500}
                        classNames="body"
                        unmountOnExit
                    >
                    <ThemeContext.Consumer>
                        {({ theme, darkMode, setGroupId, themeColor}) => (
                        <Body>
                            <Nav>
                        </Nav>
                        <Card themeColor={themeColor} title="Расписание УРТК" subtitle={<Link onClick={()=>{ localStorage.removeItem('typeShed'); }} to={'/'} style={{color:"#fff"}}>Главная<ReloadIcon/></Link>} >
                            <ListGroups themeColor={themeColor} setGroupId={setGroupId} title="Выберите группу" darkMode={darkMode} theme={theme} json={groups}/>
                        </Card>
                    </Body>
                        )}
                </ThemeContext.Consumer>
                </CSSTransition>

                <CSSTransition
                    in={isLoading}
                    timeout={500}
                    classNames="loader"
                    unmountOnExit
                >
                    <ThemeContext.Consumer>
                        {({darkMode, themeColor }) => {
                            if(error){
                                return(<Error themeColor={themeColor} mode={darkMode} localStore={'typeShed'} url={''}/>);
                            }else {
                                return(<Loader mode={darkMode} themeColor={themeColor}/>);
                            }
                        }}

                    </ThemeContext.Consumer>
                </CSSTransition>
            </>

    );
};
export default Groups;